//Fonts

@font-face {
  font-family: "digitalix";
  src: local("digitalix"), url("../fonts/digitalix.ttf") format("truetype");
  font-weight: regular;
}
@font-face {
  font-family: "Minecraft";
  src: local("Minecraft"), url("../fonts/Minecraft.ttf") format("truetype");
  font-weight: regular;
}
@font-face {
  font-family: "nokiafc22";
  src: local("nokiafc22"), url("../fonts/nokiafc22.ttf") format("truetype");
  font-weight: regular;
}


//media queries
@mixin media-max($_min-width) {
  @media screen and (max-width: $_min-width) {
      &{ @content; }
  }
}

@mixin little_screen {
  @media screen and (max-width: 1200px) {
      &{ @content; }
  }
}

//main

html,body,#root,.App{
  @include media-max(1200px){
    height: 200vh !important;
  }
}


.App {
  //overflow-y: hidden;
  margin:0;
  padding:0;
  display: flex;
  align-items: stretch;

  @include media-max(1200px){
    flex-direction: column-reverse;
  }
  #home{
    display: flex;
    width:calc(100% - 40px);
    height: 100vh;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;
    margin:0;
    padding: 0 20px;
    background-color: #ffffff;
    font-family: "digitalix";
    color: #008ce9;
    overflow-y: hidden;
    #main_home{
      display: flex;
      justify-content: start;
      flex-direction: column;
      font-size: 1.5em;
      align-items: center;
      text-align: center;
      flex-grow: 1;
      h1{
        line-height: 1.5em;
        @include media-max(1200px){
          font-size: 0.9em;
        }
      }
      h5{
        @include media-max(1200px){
          line-height: 1.5em;
          margin-bottom: 5px;
          font-size: 0.5em;
        }
      }
      div{
        display: flex;
        flex-grow: 3;
        overflow-y: hidden;
        @include media-max(1570px){
          flex-direction: column-reverse;
        }
        #photo_me{
          display: flex;
          flex-grow: 3;
          transform: translateY(50px);
          @include media-max(1570px){
            transform: translateY(10px);
            width: 80%;
            margin:auto;
          }
        }
        #footer{
          display: flex;
          flex-grow: 1;
          justify-content: center;
          align-items: center;
          flex-direction: row;
          font-size: 0.75em;
          #logo_git{
            width: 40px;
            height: 40px;
            margin-right: 10px;
          }
          a{
            text-decoration: none;
            color: initial;
          }
        }
      }
    }
  }
  .Console{
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;
    border-radius: 10px;
    width: 100%;
    margin: 2em 4em;
    padding: 10px;
    box-shadow: rgba(0,0,0,0.5) 0px 0px 25px;
    background-color: black;
    font-size: 13px;
    overflow-y: scroll;
    @include media-max(1200px){
      min-height: 50%;
      min-width: 70%;
      width: unset;
      margin: 1em;
    }
    pre{
      margin:0;
      @include media-max(1200px){
        font-size: 0.8em;
      }
    }
    .preCommand{
      margin:0;
      display: flex;
      flex-direction: row;
      .nameCommand{
        color: #008ce9;
        margin:0;
      }
    }
    code{
      display: flex;
      text-align: left;
      margin-bottom: 0.2em;
      margin-left: 1em;
    }
    #cursor{
      width: 10px;
      height: 15px;
      background-color: white;
      margin-top: 2px;
      margin-left: 4px;
  
    }
  }
}

@media (prefers-reduced-motion: no-preference) {
  #cursor {
    animation: cursor_animation infinite 0.5s cubic-bezier(1, 0, 0, 1) alternate;
  }
  #photo_me{
    //animation: blur_animation 1 10s cubic-bezier(0.0, 0.1, 0.5, 1.0);
  }
}

@keyframes cursor_animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes blur_animation {
  from {
    filter: blur(10px);
  }
  to{
    filter: blur(0);
  }
}

