@media (prefers-reduced-motion: no-preference) {
  #cursor {
    -webkit-animation: cursor_animation infinite 0.5s cubic-bezier(1, 0, 0, 1) alternate;
            animation: cursor_animation infinite 0.5s cubic-bezier(1, 0, 0, 1) alternate;
  }
}

@-webkit-keyframes cursor_animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes cursor_animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes blur_animation {
  from {
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  to {
    -webkit-filter: blur(0);
            filter: blur(0);
  }
}

@keyframes blur_animation {
  from {
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  to {
    -webkit-filter: blur(0);
            filter: blur(0);
  }
}
